import { IMaskInput } from 'react-imask';
import { forwardRef } from 'react'

const InputTextRut = forwardRef(function InputTextRut(props, ref) {
  const { onChange, ...other } = props
  return (
    <IMaskInput
      {...other}
      mask="00000000-#"
      definitions={{
        '#': /[0-9kK]/,
      }}
      inputRef={ref}
      onAccept={(value) => onChange({ target: { name: props.name, value } })}
      overwrite
    />
  )
})

export default InputTextRut